export default {
  name: "healthWarnContent",
  data() {
    return {
      list: [
        {
          name: "体温",
          unit: "℃",
          type: "101",
          class: "temperature",
          iconClass: "fa-thermometer"
        },
        {
          name: "血压",
          unit: "mmHg",
          type: "200",
          class: "diastolic-pressure",
          iconClass: "fa-heartbeat"
        },
        {
          name: "血氧",
          unit: "mmHg",
          type: "300",
          class: "oxygen",
          iconClass: "fa-eercast"
        },
        {
          name: "心率",
          unit: "BMP",
          type: "401",
          class: "heart-rate",
          iconClass: "fa-stethoscope"
