          unit: "mmol",
          type: "500",
          class: "fasting-blood-glucose",
          iconClass: "fa-tint"
        },
        {
          name: "餐后血糖",
          unit: "mmol",
          type: "501",
          class: "postprandial-blood-glucose",
          iconClass: "fa-tint"
        },
        {
          name: "心电图",
          unit: "BMP",
          type: "900",
          class: "ecg",
          iconClass: "fa-heart"
        },
      ]
    };
  },
  props: {},
  computed: {},
  methods: {},
  mounted() {}
};



